import React, { Component, Fragment } from 'react';

import {
  Card,
  Row,
  Table,
  Popconfirm,
  Col,
  Menu,
  Icon,
  Cascader,
  Select,
  Checkbox,
  Input,
} from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { listStudents } from 'dump/data';

import './style.scss';

const options = [
  {
    value: 'Hệ: Cao đẳng chính quy ',
    label: 'Hệ: Cao đẳng chính quy ',
    isLeaf: false,
  },
  {
    value: 'Hệ: Đại học chính quy ',
    label: 'Hệ: Đại học chính quy ',
    isLeaf: false,
  },
];

class FactorOfTraningScore extends Component {
  constructor(props) {
    super(props);
    this.state = {
      options,
    };
    this.title = 'Danh sách sinh viên';
    this.urlOfBtnEdit = '#/profile-student';
    this.urlOfBtnAdd = '#/factorOfTrainingScore/form/edit/';
    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
        sorter: (a, b) => a.code.length - b.code.length,
        defaultSortOrder: 'descend',
        // render: text => <Link to={``} activeClassName="active">{text}</Link>,
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
        sorter: (a, b) => a.name.length - b.name.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
        sorter: (a, b) => a.dob.length - b.dob.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Giới tính',
        dataIndex: 'gender',
        key: 'gender',
        sorter: (a, b) => a.gender.length - b.gender.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
        sorter: (a, b) => a.className.length - b.className.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'pob',
        key: 'pob',
        sorter: (a, b) => a.pob.length - b.pob.length,
        defaultSortOrder: 'descend',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          listStudents.length >= 1 ? (
            <Fragment>
              <BtnEdit href="#/profile-student" />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    // rowSelection object indicates the need for row selection
    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    this.cssBetweenRow = { marginBottom: '8px' };
  }

  onChange = (value, selectedOptions) => {
    console.log(value, selectedOptions);
  };

  loadData = selectedOptions => {
    const targetOption = selectedOptions[selectedOptions.length - 1];
    targetOption.loading = true;

    // load options lazily
    setTimeout(() => {
      targetOption.loading = false;
      targetOption.children = [
        {
          label: `Khóa 1`,
          value: 'dynamic1',
        },
        {
          label: `Khóa 2`,
          value: 'dynamic2',
        },
      ];
      this.setState({
        options: [...this.state.options],
      });
    }, 1000);
  };

  render() {
    return (
      <Card title={this.title}>
        <Row style={this.cssBetweenRow}>
          <Col span={24}>
            <Menu
              onClick={this.handleClick}
              selectedKeys={[this.state.current]}
              mode="horizontal"
              className="menu-item-form-brief"
            >
              <Menu.Item key="plus">
                <Icon type="plus-square" theme="twoTone" />
                Thêm sinh viên
              </Menu.Item>
              <Menu.Item key="reload">
                <Icon type="sync" />
                Chuyển lớp
              </Menu.Item>
              <Menu.Item key="delete">
                <Icon type="delete" theme="twoTone" twoToneColor="red" />
                Xóa sinh viên khỏi lớp
              </Menu.Item>
              <Menu.Item key="printer">
                <Icon type="printer" theme="twoTone" twoToneColor="grey" />
                In danh sách
              </Menu.Item>
              <Menu.Item key="file-excel">
                <Icon type="file-excel" theme="twoTone" twoToneColor="green" />
                Xuất excel
              </Menu.Item>
              <Menu.Item key="stop">
                <Icon type="stop" theme="twoTone" twoToneColor="red" />
                Thoát
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
        <Row style={this.cssBetweenRow} gutter={16} className="flex-center">
          <Col span={6}>
            <Cascader
              placeholder="Vui lòng chọn lớp"
              options={this.state.options}
              loadData={this.loadData}
              onChange={this.onChange}
              changeOnSelect
              style={{ width: '100%' }}
            />
          </Col>
          <Col span={3}>
            <Checkbox>Chọn tất cả</Checkbox>
          </Col>
          <Col span={6} className="flex-center">
            <span style={{ whiteSpace: 'nowrap', marginRight: '5px' }}>Họ và tên:</span>{' '}
            <Input placeholder="Họ và tên" />
          </Col>
          <Col span={6} className="flex-center">
            <span style={{ whiteSpace: 'nowrap', marginRight: '5px' }}>Sắp xếp theo:</span>
            <Select defaultValue="name" style={{ width: '70%' }}>
              <Select.Option value="name">Họ và tên</Select.Option>
              <Select.Option value="dob">Năm sinh</Select.Option>
              <Select.Option value="specialize">Khoa</Select.Option>
              <Select.Option value="pob">Nơi sinh</Select.Option>
            </Select>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              rowSelection={this.rowSelection}
              bordered
              dataSource={listStudents}
              columns={this.columns}
              onRow={() => ({
                onDoubleClick: () => {},
              })}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FactorOfTraningScore />
  </Page>
);



